import { addCommand, addCommandCallback } from '../utils';

const opentok = {
  video: {
    join: function (params: any) {
      if (params.callback) {
        return addCommandCallback('median://opentok/video/join', params);
      } else addCommand('median://opentok/video/join', params);
    },
  },
};

export default opentok;
